import {
  Component,
  OnInit,
  Input,
  ViewEncapsulation,
  Output,
  EventEmitter
} from '@angular/core';
import { SelectSearchItem } from './interface';
import { MatSelectChange } from '@angular/material';

@Component({
  selector: 'gualib-select-search',
  templateUrl: './select-search.component.html',
  styleUrls: ['./select-search.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class GualibSelectSearchComponent implements OnInit {
  @Input() itens: Array<SelectSearchItem>;
  itensFiltrados: Array<SelectSearchItem>;
  @Input() initSelectedValue: number;
  @Output() selectionChange: EventEmitter<string> = new EventEmitter<string>();

  constructor() {
  }

  ngOnInit() {
    this.filterElement('');
  }

  onSelectionChange(event: MatSelectChange) {
    this.selectionChange.emit(event.value);
  }

  filterElement(value) {
    if (this.itens) {
      this.itensFiltrados = this.itens.filter((item) => {
        return item.value.toString().toLowerCase().indexOf(value) > -1;
      });
    }
  }
}
