import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { MatDialog } from '@angular/material';
import { ImageGalleryZoomComponent } from './image-gallery-zoom/image-gallery-zoom.component';

@Component({
  selector: 'gualib-image-gallery',
  templateUrl: './image-gallery.component.html',
  styleUrls: ['./image-gallery.component.scss']
})
export class ImageGalleryComponent implements OnInit, OnChanges {

  @Input() images: string[] = [];
  @Input() closeButtonColor: string;
  @Input() closeButtonBackground: string;
  @Input() panelClass: string;
  @Input() editMode = false;
  // tslint:disable-next-line: no-output-on-prefix
  @Output() onAddImage = new EventEmitter();
  // tslint:disable-next-line: no-output-on-prefix
  @Output() onDeleteImage = new EventEmitter();
  height: string;
  @Input() disableClose = false;
  @Input() emptyImagePath = '';
  isDisplayBtn = false;

  selectedImage: string;

  constructor(public dialog: MatDialog) { }

  ngOnChanges() {
    if (this.images.length) {
      this.selectedImage = this.images[0];
    }
  }

  ngOnInit() {
    this.height = 'auto';
    if (this.images.length) {
      this.selectedImage = this.images[0];
    }
  }

  openZoom(): void {
    if (this.selectedImage && this.images.length) {
      this.dialog.open(ImageGalleryZoomComponent, {
        disableClose: false,
        height: '100%',
        width: '100%',
        maxWidth: '100vw',
        maxHeight: '100vh',
        data: {
          url: this.selectedImage,
          color: this.closeButtonColor,
          background: this.closeButtonBackground,
          disableClose: this.disableClose
        },
        panelClass: this.panelClass,
      });
    }
  }

  onClick($event) {
    this.onAddImage.emit($event);
  }

  onClickDelete($event) {
    $event.stopPropagation();
    this.onDeleteImage.emit($event);
  }
}
