import { Component, OnInit, Output, EventEmitter, ÉµConsole, ChangeDetectionStrategy, OnDestroy, Inject, ChangeDetectorRef, Input } from '@angular/core';
import {DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE, MatDateFormats} from '@angular/material/core';
import {MomentDateAdapter, MAT_MOMENT_DATE_ADAPTER_OPTIONS} from '@angular/material-moment-adapter';
import { FormControl } from '@angular/forms';
import { MatDatepicker} from '@angular/material';
import * as moment from 'moment';
import {Moment} from 'moment';
const momentAdpater = (moment as any).default ? (moment as any).default : moment;
import {CustomHeader} from './custom-header/custom-header.component';


export const MY_FORMATS = {
  parse: {
    dateInput: 'MMM  YYYY',
  },
  display: {
    dateInput: 'MMM  YYYY',
    monthYearLabel: 'YYYY',
    dateA11yLabel: 'MMM',
    monthYearA11yLabel: 'MMMM YYYY',
  },
};


@Component({
  selector: 'gualib-range-datepicker',
  templateUrl: './range-datepicker.component.html',
  styleUrls: ['./range-datepicker.component.scss'],
  providers: [
    {
        provide: DateAdapter,
        useClass: MomentDateAdapter,
        deps: [MAT_DATE_LOCALE, MAT_MOMENT_DATE_ADAPTER_OPTIONS]
    },
    {provide: MAT_DATE_FORMATS, useValue: MY_FORMATS},
]
})
export class RangeDatepickerComponent implements OnInit {

  @Output()
  returnDateEvent = new EventEmitter();
  exampleHeader = CustomHeader;
  displayButton:boolean = false;
  inicialDate = new FormControl(momentAdpater());
  finalDate = new FormControl(momentAdpater());
  validDate:boolean = true;
  errorMessage = null;
  INITIAL_DATE = "initial";
  FINAL_DATE = "final";

  @Input()
  minDate;

  @Input()
  maxDate;

  constructor() { }

  ngOnInit() {
    this.inicialDate.setValue(momentAdpater().startOf('month'));
    this.finalDate.setValue(momentAdpater().endOf('month'));
  }

  chosenYearHandler(normalizedYear: Moment, order) {
    this.disableClearButton();
    if(order === 'initial') {
      const ctrlValue = this.inicialDate.value;
      ctrlValue.year(normalizedYear.year());
      this.inicialDate.setValue(ctrlValue);
    }else {
      const ctrlValue = this.finalDate.value;
      ctrlValue.year(normalizedYear.year());
      this.finalDate.setValue(ctrlValue);
    }
  }

  chosenMonthHandler(normalizedMonth: Moment, datepicker: MatDatepicker<Moment>, order) {
    this.disableClearButton();
    if(order === 'initial') {
      const ctrlValue = this.inicialDate.value;
      ctrlValue.month(normalizedMonth.month());
      ctrlValue.startOf('month');
      this.inicialDate.setValue(ctrlValue);
      datepicker.close();
    }else {
      const ctrlValue = this.finalDate.value;
      ctrlValue.month(normalizedMonth.month());
      ctrlValue.endOf('month');
      this.finalDate.setValue(ctrlValue);
      datepicker.close();      
    }
    if(this.verifyValidDates()) {
      this.returnDateEvent.emit({"initialDate":this.inicialDate.value, "finalDate":this.finalDate.value});
    }
    
  }

  verifyValidDates():boolean {
    const initial:Moment = this.inicialDate.value;
    const final:Moment = this.finalDate.value;
    if(!initial.isBefore(final) && !initial.isSame(final)) {
      this.initialAfterFinalDate()
      this.validDate  = false;
    }else {
      this.validDate  = true;
    }
    return initial.isBefore(final);
  }

  disableClearButton() {
    const initial:Moment = this.inicialDate.value;
    const final:Moment = this.finalDate.value;
    this.validDate  = true;
    this.displayButton = !initial.isSame(final);
  }

  clear() {
    this.inicialDate = new FormControl(momentAdpater());
    this.finalDate = new FormControl(momentAdpater());
    this.returnDateEvent.emit(null);
    this.disableClearButton();
  }

  initialAfterFinalDate() {
    this.errorMessage = 'A data inicial nÃ£o pode ser superior a data final. ';
  }

  invalidDate() {
    this.errorMessage = 'Data invÃ¡lida.';
  }


  getInputValue(input) {
    if(input.inicialDate.value){
      const initial:Moment = this.inicialDate.value;
      if(this.verifyCorrectDates(initial)) {
        this.inicialDate.setValue(initial);
      }
    }
    if(input.finalDate.value) {
      const final:Moment = this.finalDate.value;
      if(this.verifyCorrectDates(final)){
        this.finalDate.setValue(final);
      }
    }
    if(this.verifyValidDates()) {
      this.returnDateEvent.emit({"initialDate":this.inicialDate.value, "finalDate":this.finalDate.value});
    }
  }

  getErrorMessage() {
    return this.errorMessage;
  }

  verifyCorrectDates(date: Moment) {
    if(!date.isValid) {
      this.invalidDate();
      return false;
    }
    return true;

  }
}

