import { Component, Input, Output, EventEmitter, ElementRef, ViewChild } from '@angular/core';

@Component({
    selector: 'gualib-search-chips-select',
    templateUrl: './search-chips-select.component.html',
    styleUrls: ['./search-chips-select.component.scss']
})
export class GualibSearchChipsSelectComponent {
    @ViewChild('inputRef') inputRef: ElementRef;

    @Input() selectValue;
    @Input() chips: Array<{ key, value }> = [];
    inputValue = '';
    @Input()
    get input() {
        return this.inputValue;
    }
    set input(val) {
        this.inputValue = val;
    }
    @Output()
    inputChange = new EventEmitter();
    @Input() selectLabel = '';
    @Input() selectChoices: Array<{ key, value }> = [];
    // tslint:disable-next-line: no-output-on-prefix
    @Output() onChangeSelect = new EventEmitter();
    // tslint:disable-next-line: no-output-on-prefix
    @Output() onCloseChip = new EventEmitter();
    // tslint:disable-next-line: no-output-on-prefix
    @Output() onSearch = new EventEmitter();

    onClickCloseChip(chipKey: string, $event: MouseEvent) {
        $event.stopPropagation();
        this.onCloseChip.emit(chipKey);
    }

    onInputFocus() {
        this.inputRef.nativeElement.focus();
    }

    onClickSearch() {
        this.onSearch.emit(this.inputValue);
    }

    onSelectionChange() {
        this.onChangeSelect.emit(this.selectValue);
    }

    onKeyUp($event: KeyboardEvent) {
        this.inputChange.emit(this.inputValue);
        if ($event.code === 'Enter') {
            this.onClickSearch();
        }
    }
}
